html, body, ul, li, p, img, h1, h2, h3, h4 {
  margin: 0;
  padding: 0; }

ul {
  list-style: none; }

a {
  text-decoration: none;
  transition: 0.5s ease; }

img {
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
  border: 0;
  vertical-align: middle; }

.promo, .calculator {
  position: relative; }
  .promo:after, .calculator:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5); }

html {
  font: 18px 'Open Sans', sans-serif;
  color: #333; }

.justify {
  text-align: justify;
  font-size: 1px;
  line-height: 0;
  text-justify: newspaper;
  zoom: 1; }
  .justify:after {
    width: 100%;
    height: 0;
    visibility: hidden;
    overflow: hidden;
    content: '';
    display: inline-block; }

.promo__slogan {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%); }

.mobile-control, .promo__video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.wraper {
  position: relative;
  width: 1182px;
  margin: 0 auto; }
  .wraper_promo, .wraper_video {
    width: 800px; }

.button {
  display: inline-block;
  cursor: pointer;
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  text-align: center;
  white-space: nowrap;
  color: #323232;
  font-size: 24px;
  line-height: 48px;
  height: 50px;
  padding: 0 11px;
  border-radius: 4px; }
  .button_promo {
    background-color: #e23101;
    background-color: #357ebd;
    color: #fff;
    margin: 30px 0 0 0; }

.header {
  font-size: 40px;
  line-height: 1.3;
  margin: 0 0 50px 0; }

.text {
  line-height: 36px; }
  .text span {
    display: block; }

/*head*/
.head {
  display: table;
  box-shadow: 0px 6px 6px -5px rgba(0, 0, 0, 0.36);
  height: 70px;
  color: #fff;
  position: fixed;
  width: 100%;
  z-index: 2;
  top: 0;
  left: 0;
  background-color: #0a0a0a;
  font-size: 20px; }
  .head.white-block {
    background: #fff;
    color: #252525; }
    .head.white-block .menu__link {
      color: #252525; }
    .head.white-block .phone {
      color: #252525; }
    .head.white-block .mobile-control {
      background: #0a0a0a; }
      .head.white-block .mobile-control:after, .head.white-block .mobile-control:before {
        background: #fff; }

.logo {
  float: left;
  width: 254px;
  z-index: 3;
  padding: 3px 0 3px 30px; }
  .logo__link {
    display: block; }

.mobile-control:after, .mobile-control:before {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 20%;
  background: #0a0a0a;
  transform: translateZ(0); }

.mobile {
  display: none;
  position: relative;
  float: right;
  height: 70px;
  right: 0;
  width: 70px; }
  .mobile-control {
    width: 26px;
    height: 20px;
    background: #fff;
    z-index: 1; }
    .mobile-control:after {
      top: 60%; }
    .mobile-control:before {
      top: 20%; }

.menu {
  position: relative;
  z-index: 2;
  height: 100%;
  text-align: center; }
  .menu__list {
    display: inline-block;
    vertical-align: middle;
    line-height: 70px; }
  .menu__item {
    display: inline-block;
    margin-left: 20px;
    font-size: 20px; }
  .menu__link {
    color: #fff; }

.phone {
  position: relative;
  z-index: 2;
  display: table;
  height: 60px;
  float: right;
  width: 250px;
  text-align: right;
  border-left: 1px dotted #555;
  padding: 0 30px;
  color: #fff;
  box-sizing: border-box;
  margin: 5px 0;
  transition: 0.5s ease; }
  .phone__number {
    display: table-cell;
    vertical-align: middle; }

/*head end*/
/*promo*/
.promo {
  position: relative;
  margin: 70px 0 0 0;
  color: #fff;
  overflow: hidden;
  height: 550px; }
  .promo .wraper_promo {
    height: 100%; }
  .promo__content {
    position: relative; }
  .promo__list {
    display: inline-block;
    text-align: left; }
  .promo__slogan {
    left: 0;
    z-index: 1;
    box-sizing: border-box;
    width: 100%; }
  .promo__video {
    height: 100%;
    max-width: none; }
  .promo__text {
    background: url(../img/arrow.png) no-repeat -7px -196px;
    padding: 0 0 0 30px; }
  .promo__tv {
    background: url(../img/video.png) no-repeat 0 0;
    right: 0;
    position: absolute;
    bottom: 50%;
    transform: translate(0, 50%);
    width: 300px;
    height: 200px;
    background-size: contain;
    z-index: 1;
    text-align: center; }
  .promo__play {
    display: inline-block;
    width: 100px;
    height: 100px;
    background: url(../img/play.png) no-repeat 0 0;
    background-size: contain;
    border: none;
    margin: 50px 0;
    box-sizing: border-box;
    outline: none; }

.container {
  text-align: center; }

.sub-title {
  font-size: 40px;
  padding: 0 0 15px 0;
  text-align: left; }
  .sub-title span {
    display: block; }

.header.header_slogan {
  text-align: left;
  font-size: 50px;
  margin: 0 0 10px 0; }

/**/
/*service advantages*/
.advantages {
  font-size: 0px;
  text-align: center;
  padding: 50px 0 0 0; }

.service {
  display: inline-block;
  width: 27%;
  margin: 0 3% 50px 3%;
  box-sizing: border-box;
  vertical-align: top;
  text-align: center; }
  .service__images {
    width: 150px; }
  .service__title {
    margin: 20px 0 0 0;
    font-size: 25px;
    line-height: 1.1;
    color: #363636;
    height: 54px; }
  .service__description {
    font-size: 15px;
    margin: 9px 0 0 0; }
  .service .advantages_description .service__title {
    font-size: 22px; }

/*advantages end*/
/*gallery*/
.gallery {
  font-size: 0px;
  text-align: center;
  background-color: #f2f2f2;
  padding: 50px 0; }

.photo {
  display: inline-block;
  position: relative;
  width: 25%;
  box-sizing: border-box;
  padding: 0 10px;
  vertical-align: top; }
  .photo__title {
    font-size: 18px;
    line-height: 1;
    background: #452e2e;
    letter-spacing: -0.2px;
    padding: 10px 0;
    color: #fff; }
  .photo__link {
    display: block; }
    .photo__link:after {
      content: '';
      position: absolute;
      top: -16px;
      left: -7px;
      width: 0;
      height: 0;
      border: 15px solid transparent;
      border-bottom: 15px solid #F2F2F2;
      transform: rotate(-45deg); }

.row-photo {
  border-bottom: 1px solid #d1d1d1;
  padding: 0 0 35px 0; }
  .row-photo:last-child {
    border: none;
    padding: 35px 0 0 0; }

/*video*/
.video {
  text-align: center;
  padding: 50px 0;
  background: #ECECEC; }
  .video__text {
    text-align: justify;
    font-size: 18px;
    line-height: 1.8; }
  .video__content {
    padding: 50px 0 0 0;
    width: 100%;
    height: 315px;
    margin: 0 auto; }
    .video__content iframe {
      width: 100%;
      height: 100%; }

/*video end*/
/*calculator*/
.calculator {
  text-align: center;
  height: 550px;
  background: url(../img/m-calc-bg.jpg) no-repeat 0 0;
  background-size: cover;
  background-attachment: fixed; }

.wraper_calculator {
  z-index: 1;
  top: 50%;
  transform: translate(0%, -50%); }

.form-calculator {
  width: 46%;
  background: #efe9dd;
  border-radius: 4px;
  margin: 0 auto;
  padding: 50px; }

.header_calculator {
  margin: 0 0 25px 0;
  line-height: 1; }

.item-form {
  display: inline-block;
  margin: 0 0 25px 0;
  width: 100%;
  font-size: 0;
  text-align: center; }
  .item-form__title {
    display: block;
    height: 40px;
    line-height: 40px;
    vertical-align: top;
    font-size: 20px;
    font-weight: 600;
    box-sizing: border-box;
    text-align: center; }
    .item-form__title_checkbox {
      display: inline-block; }
  .item-form__field {
    display: inline-block;
    width: 100%;
    padding: 0px 25px 0 10px;
    height: 45px;
    font-size: 20px;
    border: 1px solid #333;
    border-radius: 5px;
    box-shadow: 0px 0px 0px 1px #333; }
  .item-form__fireplace {
    display: inline-block;
    position: relative;
    vertical-align: top;
    text-align: left;
    line-height: 1.3;
    width: 40%;
    margin: 0; }
  .item-form.justify {
    margin: 0;
    text-align: justify; }

.checkbox {
  display: none; }
  .checkbox:checked ~ .fireplace {
    background: url(../img/arrow.png) no-repeat center -287px; }

.select-arrow {
  position: relative;
  background: url(../img/arrow.png) no-repeat right -157px, #fff;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none; }
  .select-arrow::-ms-expand {
    display: none; }
  .select-arrow:active, .select-arrow:focus {
    border: 1px solid #333; }

.price {
  width: 59%;
  text-align: right;
  display: inline-block;
  vertical-align: top;
  line-height: 1.3; }
  .price__item {
    max-width: 1019px;
    font-weight: 600;
    font-size: 20px; }
  .price__value {
    font-size: 25px;
    font-weight: 700; }

.fireplace {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 1px solid #333;
  border-radius: 5px;
  box-shadow: 0px 0px 0px 1px #333;
  margin: 0 0 0 10px; }

/*calculator end*/
/*catalog*/
.catalog {
  padding: 50px 0;
  font-size: 0; }

.header_catalog {
  text-align: center; }

.model {
  display: inline-block;
  width: 50%;
  box-sizing: border-box;
  padding: 0 45px;
  vertical-align: top;
  border-right: 1px dotted #452E2E; }
  .model:last-child {
    border: none; }
  .model__title {
    font-size: 24px;
    line-height: 1;
    background: #452e2e;
    letter-spacing: -0.2px;
    padding: 15px 0;
    color: #fff;
    text-align: center; }
  .model__text {
    font-size: 18px;
    line-height: 1.8;
    margin: 25px 0 0 0;
    text-align: justify; }
  .model__link {
    display: block; }

/*catalog end*/
/*review*/
.review {
  background: #ECECEC;
  padding: 50px 0; }
  .review__title {
    font-size: 30px;
    font-weight: 600;
    padding: 0 0 25px 0; }
    .review__title span {
      display: block;
      font-size: 25px; }
  .review__text {
    line-height: 36px;
    text-align: justify; }
  .review__content {
    padding: 0 0 0 350px;
    box-sizing: border-box;
    background: url(../img/men.jpg) no-repeat 0 bottom; }
  .review__images {
    display: none;
    min-width: 38%;
    margin: 0 auto;
    max-width: 60%; }

.header_review {
  text-align: left; }

/*review end*/
/*footer*/
.footer {
  padding: 25px 0;
  background-color: #222;
  color: #fff; }

.copyright {
  text-align: center; }
  .copyright__item {
    font-size: 14px;
    line-height: 1.7; }
    .copyright__item__develop {
      font-size: 14px; }
  .copyright a {
    color: #fff; }

